html {
  height: 100%;
}
@font-face {
  font-family: "Bamin";
  src: url(./assets/BMJUA_ttf.ttf) format("truetype");
}
@font-face {
  font-family: "ssang";
  src: url(./assets/Typo_SsangmunDongB.ttf) format("truetype");
}

body {
  margin: 0;
  font-family: "ssang";
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

li {
  padding: 0;
  margin: 0;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.navigation-uparrow {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #14406c;
  top: 6px;
  left: 45%;
  position: absolute;
}

.submenu {
  display: none;
  position: absolute;
  background-color: "#14406c";
  padding-top: 16px;
  animation: fadein 0.2s;
}
.submenu2 {
  display: none;
  position: absolute;
  transform: translateX(-19px);
  background-color: "#14406c";
  padding-top: 16px;
  animation: fadein 0.2s;
}
.submenu3 {
  display: none;
  position: absolute;
  transform: translateX(-70px);
  background-color: "#14406c";
  padding-top: 16px;
  animation: fadein 0.2s;
}
.navigation-submenu-button:hover {
  background-color: white;
  color: #14406c;
}
